* {
    box-sizing: border-box;
}

.app {
    font-size: 30px;
    display: flex;
    justify-content: center;
    width: 100%;
    min-height: 100vh;
    margin: 0 auto;
    display: flex;
    background-color: #fa9a4c;
    background: #212121;
}

.todo-list {
    width: 70%;
}

.create-task-box {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80px;
}

.add-task-textarea {
    width: 80%;
}

.add-task-button {
    width: 15%;
    height: 40px;
}

.line {
    width: 100%;
    height: 1px;
    background: rgb(71, 62, 187);
}

.task-box-inner {
    display: flex;
    justify-content: space-between;
}

.task-name {
    width: 87%;
    word-wrap: break-word;
}

.task-buttons {
    width: 120px;
}

.modal-window {
    display: flex;
}

.modal-task-name {
    width: 100%;
    display: flex;
    margin-bottom: 30px;
    justify-content: center;

    font-size: 30px;
    word-wrap: break-word;
}

.name-changer {
    width: 100%;
    text-align: center;
}

.modal-bottom {
    width: 100%;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

.date-picker {
    width: 100%;
}
